/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.8rem;
  color: white;
}

p {
  font-size: 2rem;
  margin: 0 0 2rem 0;
}

[class*=container],
.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

[class*=__heading] {
  color: #00b7ff;
}

img {
  max-width: 100%;
  display: block;
  pointer-events: none;
}

ul {
  margin: 0;
}

.section {
  padding: 3rem 0;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

input {
  text-indent: 0.5rem;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3.2rem;
}

[class$=heading] {
  margin-bottom: 2rem;
}

a {
  text-decoration: none;
}

/** Media Queries **/
.hero {
  background-image: url("../img/earth_bg.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  row-gap: 3rem;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 5rem;
  border-bottom: 1px solid #00b7ff;
  padding: 1rem 0;
}

.navigation {
  display: flex;
  gap: 2rem;
}

.navigation__link {
  text-decoration: none;
  color: white;
  font-size: 2.5rem;
}

.content {
  margin: 0 auto;
  padding: 3rem;
  max-width: 100rem;
  width: 100%;
  border: 1px solid #00b7ff;
  background-color: rgba(0, 0, 0, 0.7);
}

.form {
  display: grid;
  width: 70%;
  grid-template-columns: 30% 30%;
  row-gap: 1rem;
  padding-top: 2rem;
  align-items: center;
}

.form__button {
  background-color: #00b7ff;
  color: black;
  padding: 0.5rem 0;
  margin-top: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  transition: background-color;
  transition-duration: 300ms;
  cursor: pointer;
}

@media (min-width: 768px) {
  .form__button {
    width: auto;
  }
}
.form__button:hover {
  background-color: #0092cc;
}

.footer {
  display: flex;
  justify-content: center;
  border-top: 1px solid rgb(0, 183, 255);
}

.footer__rights {
  padding-top: 1rem;
  font-size: 1.5rem;
}

.table {
  width: 100%;
  border-collapse: collapse;
}

thead {
  text-align: left;
}

td {
  padding: 1rem 0.5rem;
}

td:nth-child(1) {
  padding: 0 2rem;
}

td:nth-child(5) {
  min-width: 10rem;
}

tr {
  border-bottom: 1px solid white;
}

#paginator {
  margin: 0;
  display: flex;
  justify-content: center;
  gap: 3rem;
}

#paginator .arrow {
  margin: 0;
  padding: 0;
  font-size: 3rem;
  font-weight: 700;
  color: #00b7ff;
  cursor: pointer;
}

.spinner {
  width: 50px;
  height: 40px;
  text-align: center;
  font-size: 10px;
  margin: 0 auto;
}

.spinner > div {
  background-color: #00b7ff;
  height: 100%;
  width: 8px;
  margin-left: 2px;
  display: inline-block;
  -webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
  animation: sk-stretchdelay 1.2s infinite ease-in-out;
}

.spinner .rect2 {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}

.spinner .rect3 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

.spinner .rect4 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}

.spinner .rect5 {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}

@-webkit-keyframes sk-stretchdelay {
  0%, 40%, 100% {
    -webkit-transform: scaleY(0.4);
  }
  20% {
    -webkit-transform: scaleY(1);
  }
}
@keyframes sk-stretchdelay {
  0%, 40%, 100% {
    transform: scaleY(0.4);
    -webkit-transform: scaleY(0.4);
  }
  20% {
    transform: scaleY(1);
    -webkit-transform: scaleY(1);
  }
}